.jplayer{
	position: relative;
	padding-top: 56.27%;
	background: #f2f2f2;
	&-wrapper{
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
	}
	&-room{
		height: 22px;
		line-height: 22px;
		position: absolute;
		right: 8px;
		top: 9px;
		background: rgba(255,255,255,.24);
		color: rgba(255,255,255,.4);
		font-size: 12px;
		z-index: 999;
		padding: 0px 6px;
		border-radius: 22px;
		text-align: center;
		span{
			display: inline-block;
			transform: scale(0.8);
		}
	}
}
.jplayer-none{
	background: black;
	.jplayer-room{
		color: rgba(255,255,255,.4)
	}
	&__text{
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		color: #F5A623;
		text-align: center;
	}
}