.detail{
	&-top{
		width: 100%;
		height: 55px;
		background: white;
		display: flex;
		align-items: center;
		&__logo{
			width: 100px;
			height: 20px;
			background-image: url('../images/logo.png');
			margin-left: 15px;
		}
		&__button{
			flex: 1;
			text-align: right;
			display: flex;
			justify-content: flex-end;
			&-home{
				font-size: 12px !important;
				width: 74px !important;
				height: 27px !important;
				line-height: 28px !important;
				border-radius: 4px !important;
			}
			.open{
				background: white !important;
				color: #F5A623 !important;
				margin: 0 15px 0 10px;
				&.am-button-active{
					background: #fafafa !important;
				}
			}
		}
	}
	&-live{
		background: white;
		padding: 15px;
		display: flex;
		&__header{
			width: 55px;
			display: flex;
			align-items: center;
			&-image{
				width: 55px;
				height: 55px;
				border-radius: 50%;
			}
		}
		&__center{
			margin-left: 15px;
			flex: 1;
			&-name{
				font-size: 16px;
				color: #333;
				margin-bottom: 14px;
			}
			&-number{
				color: #999999;
				font-size: 14px;
				margin-bottom: 14px;
			}
			&-hint{
				color: #999999;
				font-size: 14px;
				&-line{
					display: inline-block;
					width: 1px;
					height: 15px;
					background: rgba(0,0,0,.2);
					vertical-align: middle;
					margin: -2px 7px 0;
				}
				&-hits{
					color: #666666;
				}
			}
		}
		&__follow{
			width: 72px;
			height: 100%;
			&-button{
				height: 30px !important;
				line-height: 32px !important;
				font-size: 14px !important;
				span{
				}
			}
		}
	}
	&-notice{
		padding: 0 15px;
		background: white;
		&__text{
			color: #666666;
			font-size: 14px;
			padding: 15px 0;
			border-top: 1px solid rgba(0,0,0,.05);
		}
	}
	&-more{
		padding: 5px 27px 15px;
		a{
			font-size: 15px !important;
		}
	}
}






